import robbyImageUpload from '../../components/robby-image-upload/robby-image-upload.vue'
import {ajax} from '../../common/js/util.js'
import toast from '../../common/js/toast.js'
import global_ from '../../common/js/global.js'
export default {
	data() {
		return {
			show: false,
			radio: 1,
			user: '',
			card: '',
			tel: '',
			address: '',
			content: '',
			location: '上传位置',
			imageData: [],
			url: global_.baseUrl
		}
	},
	components: {
		robbyImageUpload
	},
	onShow() {
		if(this.$store.state.location){
			this.location = this.$store.state.location
		}
	},
	methods: {
		shows() {
			this.show = true
		},
		RadioChange(e) {
			this.radio = e.detail.value
			this.show = false
		},
		control() {
			this.show = false
		},
		deleteImage(obj) {
			console.log(obj)
		},
		addImage(obj) {
			console.log(this.imageData)
			console.log(obj)
		},
		inputList(list) {
			console.log(list)
		},
		goMap() {
			uni.navigateTo({
				url: '../map/map'
			})
		},
		submit() {
			if(this.user == ''){
				toast.showTitle('姓名不能为空')
				return
			}
			
			let idcardReg = /^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/
			if(this.card == ''){
				toast.showTitle('身份证不能为空')
				return
			}else if(!(idcardReg.test(this.card))){
				toast.showTitle('身份证不合法')
				return
			}
			
			if(this.tel == ''){
				toast.showTitle('手机号不能为空')
				return
			}else if(!(/^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/.test(this.tel))){
				toast.showTitle('手机号格式不正确')
				return
			}
			
			if(this.address == ''){
				toast.showTitle('地址不能为空')
				return
			}
			
			if(this.content == ''){
				toast.showTitle('举报内容不能为空')
				return
			}
			
			if(this.content.length > 300){
				toast.showTitle('举报内容不能超过300字')
				return
			}
			
			if(this.imageData.length == 0){
				toast.showTitle('请上传图片')
				return
			}
			
			if(this.location == '上传位置'){
				toast.showTitle('请上传位置')
				return
			}
			
			let self = this
			let arr = this.imageData
			for(let i=0;i<arr.length;i++){
				if(arr[i].indexOf(this.url) > -1){
					arr[i] = arr[i].substr(this.url.length)
				}
			}
			let files = arr.join('|')
			let datas = {
				uid: uni.getStorageSync('uid'),
				sex: this.radio,
				aname: this.user,
				idnum: this.card,
				telnum: this.tel,
				address: this.address,
				content: this.content,
				material: files,
				location: this.location,
				latitude: this.$store.state.lat,
				longitude: this.$store.state.lon
			}
			toast.showLoading('加载中...')
			let data = {
				url: '/appealPenal/submit',
				data: datas,
				header: {
					"Content-Type": "application/x-www-form-urlencoded"
				},
				success: function(res) {
					if (res.data.success == true) {
						toast.hideLoading()
						self.clear()
						toast.showTitle('提交成功')
						setTimeout(function(){
							uni.switchTab({
								url: '/pages/index/index'
							})
						},500)
					}
				}
			}
			ajax(data)
		},
		clear() {
			this.radio = 1
			this.user = ''
			this.card = ''
			this.tel = ''
			this.address = ''
			this.content = ''
			this.location = '上传位置'
			this.imageData = []
			this.$store.commit('changeLocation', '')
		}
	}
}
